.password-gate-wrapper {
    height: 100vh;
    width: 100%;
    background: linear-gradient(145deg, #0f0f17, #1e1e2f);
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Patrick Hand', cursive;
  }
  
  .password-gate-card {
    background: rgba(255, 255, 255, 0.05);
    padding: 40px 30px;
    border-radius: 20px;
    backdrop-filter: blur(12px);
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.06);
    text-align: center;
    color: #fff;
    max-width: 420px;
    width: 90%;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .password-gate-card h2 {
    margin-bottom: 10px;
    font-size: 26px;
  }
  
  .password-gate-card .subtitle {
    font-size: 15px;
    opacity: 0.7;
    margin-bottom: 20px;
  }
  
  .password-gate-card input {
    width: 100%;
    padding: 12px 16px;
    margin-bottom: 16px;
    border-radius: 12px;
    border: none;
    font-size: 16px;
    background: rgba(255, 255, 255, 0.1);
    color: white;
    outline: none;
    transition: 0.3s ease;
  }
  
  .password-gate-card input:focus {
    background: rgba(255, 255, 255, 0.15);
  }
  
  .password-gate-card button {
    width: 100%;
    padding: 12px;
    background: #5c5cff;
    border: none;
    border-radius: 12px;
    color: white;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s ease;
  }
  
  .password-gate-card button:hover {
    background: #7878ff;
  }
  
  .password-gate-card .error {
    color: #ff6b6b;
    margin-top: 12px;
    font-size: 14px;
  }
  